import React from 'react';
import './Style.css';   

function RowItems(props)
{
    const items = props.Items;
    const listItems = items.map(item => {
        return(
            <tr key={item.id}>
                <td>{item.id}</td>
                <td>{item.Desc}</td>
                <td>{item.expAmt}</td>
                <td><button onClick={(e)=>props.deleteRow(item.id,item.expAmt)}>X</button></td>
            </tr>   
        )
    });

    return (
        <tbody>
            {listItems}
        </tbody>    
    )
}

export default RowItems;