import React from 'react';
import Budget from './Images/Budget.png';
import Balance from './Images/Balance.png';
import Expense from './Images/Expense.png';
import './Style.css';

export default class Home extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            currBudget:""
        }
        this.inputHandler = this.inputHandler.bind(this);
    }
    inputHandler(e)
    {
        this.setState({
            currBudget:e.target.value,
        });  
    }

    render(){
        return(
            <div className="home-card container-fluid">
                <div className="card container-fluid">
                    <div className="container container-fluid">
                        <h3>Enter Your Budget:</h3>
                        <input type="number" placeholder="Enter Amount: " onChange={this.inputHandler}
                        value={this.props.currBudget}
                        />
                        <button onClick={()=> this.props.addData(this.state.currBudget)}>Add</button>
                    </div>
                </div>
                <div className="data-card container-fluid">
                    <div className="lblNames">
                    <label>Budget</label>
                    <label>Expenses</label>
                    <label>Balance</label>
                    </div>
                    <div className="imgdiv container-fluid">
                        <img src={Budget} alt=""></img>
                        <img src={Expense} alt=""></img>
                        <img src={Balance} alt=""></img>
                    </div>
                    <div className="lblNames container-fluid">
                        <label>{this.props.budgetAmt}</label>
                        <label className="expAmt">{this.props.totalExpenseAmt===0 ? 0 : "-"+this.props.totalExpenseAmt}</label>
                        <label>{this.props.balanceAmt}</label>
                    </div>
                </div>
            </div> 
        )
    }
}
