import React from 'react';
import Budget from './Images/Budget.png';
import Balance from './Images/Balance.png';
import ExpenseImg from './Images/Expense.png';
import './Style.css';
import RowItems from './RowItems';
export default class Expense extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            desc : "",
            expAmt :""

        };

        this.inputHandler = this.inputHandler.bind(this);
        this.addExp = this.addExp.bind(this);

    }

    addExp()
    {

        this.props.addExpenses(this.state.desc,this.state.expAmt);
        this.setState({
            desc : "",
            expAmt :""

        });
    }

    inputHandler(e)
    {
        this.setState({
            [e.target.name]:e.target.value,
        });  
    }

    tableHeader()
    {
        return (
            <thead>
                <tr>
                    <th>ID</th>
                    <th>Description</th>
                    <th>Amount</th>
                </tr>
            </thead>
        )
    }
    render(){
        return(
            <div className="home-card container-fluid">
                <div className="exp-card container-fluid">
                    <div className="container container-fluid">
                        <h3>Enter Expense Amount:</h3>
                        <input type="number" placeholder="Enter Amount: " name="expAmt" onChange={this.inputHandler}
                        value={this.props.currBudget}
                        />
                        <h3>Expense Description:</h3>
                        <input type="text" placeholder="Enter Description: " name="desc" onChange={this.inputHandler}
                        value={this.props.currBudget}
                        />
                    
                        <button onClick={this.addExp}>Add</button>
                    </div>
                </div>
                <div className="exp-data-card container-fluid">
                    <div className="exp-lblNames container-fluid">
                    <label>Budget</label>
                    <label>Expenses</label>
                    <label>Balance</label>
                    </div>
                    <div className="exp-imgdiv container-fluid">
                        <img src={Budget} alt=""></img>
                        <img src={ExpenseImg} alt=""></img>
                        <img src={Balance} alt=""></img>
                    </div>
                    <div className="exp-lblNames container-fluid">
                        <label>{this.props.budgetAmt}</label>
                        <label className="expAmt">{this.props.totalExpenseAmt===0 ? 0 : "-"+this.props.totalExpenseAmt}</label>
                        <label>{this.props.balanceAmt}</label>
                    </div>
                    <div className="exp-table container-fluid">
                        <table>
                            {this.props.totalExpenseAmt===0 ? null : this.tableHeader()}
                            <RowItems  Items={this.props.expenseData} 
                            deleteRow = {this.props.deleteRow}
                            />
                        </table>
                    </div>
                </div>
            </div>
        )
    }
}
