import React from 'react';
import {BrowserRouter, Route,Switch} from 'react-router-dom';
import Expense from './Expense';
import Home from './Home';
import NavBar from './Navbar';

export default class MainPage extends React.Component
{
    constructor()
    {
        super();
        this.state = {
            expenseData :[
            ],
            
            budgetAmt:0,
            totalExpenseAmt:0,
            balanceAmt:0,
            
            currBudget:"",
            currExpense:"",
            currDesc:""
        };

       
        this.addData = this.addData.bind(this);
        this.addExpenses = this.addExpenses.bind(this);
        this.deleteItem = this.deleteItem.bind(this);
    }
    
    addData(bAmt)
    {
        const cBud = bAmt;
        if(cBud>0)
        {
            const newBud = Number(this.state.budgetAmt) + Number(bAmt);
            const newBal = Number(this.state.balanceAmt) + Number(bAmt); 
            const newExp = Number(this.state.totalExpenseAmt);
            this.setState({
                
                budgetAmt:newBud,
                totalExpenseAmt:newExp,
                balanceAmt:newBal,
                currBudget:""
            })
        }
        else
        {
            this.setState({
                currBudget:""
            })
            alert("Enter Budget amount greater than zero");
        }
    }

    addExpenses(desc,amt)
    {
        if(desc.trim()!=="" && amt>0)
        {
            if(this.state.balanceAmt - amt >=0)
            {
                const newRow = {
                    id: this.state.expenseData.length +1,
                    Desc: desc,
                    expAmt: amt
                };
                const newBalance = Number(this.state.balanceAmt) - Number(amt);
                const newExpAmt = Number(this.state.totalExpenseAmt) + Number(amt);
                const newExpData = [...this.state.expenseData,newRow];
                this.setState({
                    expenseData:newExpData,
                    
                        totalExpenseAmt: newExpAmt,
                        balanceAmt:newBalance,
                    currBudget:""
                });

            }
            else
            {
                this.setState({
                    currBudget:""
                })
                alert("You don't have sufficent Balance");
            }
        }
        else{
            alert("All Fields are necessary");
        }
    }

    // setUpdate(name,itemValue,itemKey)
    // {
    //     if(itemValue.trim()!=="")
    //     {
    //         let Nitems = this.state.expenseData;
    //         Nitems.map(item => {
    //             if(itemKey===item.id){
    //                 item[name] = itemValue;
    //                 if()
    //             }
    //         });

    //         this.setState({
    //             jobData : Nitems
    //         });
    //     }
    //     else
    //     {
    //         alert("Fields must not be empty");
    //     }
    // }

    deleteItem(key,amt)
    {
        const filteredItem = this.state.expenseData.filter(item => item.id!==key);
        const newExpAmt = Number(this.state.totalExpenseAmt) - Number(amt);
        const newBalance = Number(this.state.balanceAmt) + Number(amt);
        this.setState({
            expenseData: filteredItem,
            totalExpenseAmt : newExpAmt,
            balanceAmt : newBalance
        });
    }

    render(){
        return(
            <div className="mainPage container-fluid">
                
                <BrowserRouter>
                    <NavBar />
                    <h1 align="center">Expense Tracker</h1>
                    <Switch>
                        <Route exact path="/ExpenseTracker" component={() => {
                            return(
                                <Home inputHandler = {this.inputHandler}
                                addData = {this.addData}
                                budgetAmt = {this.state.budgetAmt}
                                totalExpenseAmt = {this.state.totalExpenseAmt}
                                balanceAmt = {this.state.balanceAmt}
                                />
                            )
                        }}/>
                        <Route exact path="/" component={() => {
                            return(
                                <Home inputHandler = {this.inputHandler}
                                addData = {this.addData}
                                budgetAmt = {this.state.budgetAmt}
                                totalExpenseAmt = {this.state.totalExpenseAmt}
                                balanceAmt = {this.state.balanceAmt}
                                />
                            )
                        }}/>
                        <Route exact path="/expenses" component={()=>{
                            return(
                                <Expense 
                                currExpense = {this.state.currExpense}
                                currDesc = {this.state.currDesc}
                                budgetAmt = {this.state.budgetAmt}
                                totalExpenseAmt = {this.state.totalExpenseAmt}
                                balanceAmt = {this.state.balanceAmt}
                                expenseData = {this.state.expenseData}
                                addExpenses = {this.addExpenses}
                                deleteRow = {this.deleteItem}
                                />
                            )
                        }} />   
                    </Switch>
                </BrowserRouter>
            </div>
        )
    }
}